import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './UserProfilePage.css';
import { motion, AnimatePresence } from 'framer-motion';

const ITEMS_PER_PAGE = 30;

const UserProfilePage = () => {
  // State hooks to manage user input, profile data, repositories, errors, and pagination
  const [username, setUsername] = useState('');
  const [userData, setUserData] = useState(null);
  const [repositories, setRepositories] = useState([]);
  const [error, setError] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalRepositories, setTotalRepositories] = useState(0);
  const [followers, setFollowers] = useState([]); // State for storing followers

  // Fetches user profile data from GitHub API based on the entered username
  const fetchUserProfile = async () => {
    if (!username) return;
    try {
      const userResponse = await axios.get(`https://api.github.com/users/${username}`);
      setUserData(userResponse.data);
      setError('');
      setCurrentPage(1);
      await fetchUserRepositories(1); // Reset to the first page
      await fetchFollowers(); // Fetch followers when loading the profile
    } catch (err) {
      setUserData(null);
      setRepositories([]);
      setFollowers([]);
      setError(
        err.response && err.response.status === 404
          ? 'User not found. Please try a different username.'
          : 'An error occurred. Please try again later.'
      );
    }
  };

  // Fetches repositories of the user based on the current page number
  const fetchUserRepositories = async (page) => {
    try {
      const reposResponse = await axios.get(
        `https://api.github.com/users/${username}/repos?page=${page}&per_page=${ITEMS_PER_PAGE}`,
        {
          headers: { Accept: 'application/vnd.github.v3+json' },
        }
      );

      // Update total repository count if available in the response headers
      const totalRepos = reposResponse.headers['x-total-count'];
      if (totalRepos) {
        setTotalRepositories(parseInt(totalRepos, 10));
      } else {
        setTotalRepositories(0);
      }

      setRepositories(reposResponse.data);
      setError(reposResponse.data.length === 0 ? 'No public repositories on this page.' : '');
    } catch (err) {
      setRepositories([]);
      setError('Failed to fetch repositories! Please try again later.');
      console.error('Error fetching repositories:', err);
    }
  };

  // Fetches followers of the user from the GitHub API
  const fetchFollowers = async () => {
    try {
      const response = await axios.get(`https://api.github.com/users/${username}/followers`);
      setFollowers(response.data);
    } catch (err) {
      setFollowers([]);
      console.error('Error fetching followers:', err);
    }
  };

  // Triggers user profile search when the Enter key is pressed
  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      fetchUserProfile();
    }
  };

  // Handles moving to the next page of repositories
  const handleNextPage = () => {
    const nextPage = currentPage + 1;
    setCurrentPage(nextPage);
    fetchUserRepositories(nextPage);
  };

  // Handles moving to the previous page of repositories
  const handlePreviousPage = () => {
    if (currentPage > 1) {
      const previousPage = currentPage - 1;
      setCurrentPage(previousPage);
      fetchUserRepositories(previousPage);
    }
  };

  // Calculate the total number of pages for pagination based on the total repositories
  const totalPages = userData && userData.public_repos ? Math.ceil(userData.public_repos / ITEMS_PER_PAGE) : 0;

  return (
    <motion.div
      className="user-profile-container"
      layout
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <h1>GitHub User Profile</h1>
      <div className="input-section">
        <input
          type="text"
          value={username}
          placeholder="Enter GitHub username"
          onChange={(e) => setUsername(e.target.value)}
          onKeyDown={handleKeyDown}
        />
        <button onClick={fetchUserProfile}>Search</button>
      </div>
      {error && <p className="error-message">{error}</p>}
      <div className='top-container'>
        {/* User Details Section */}
        <AnimatePresence>
          {userData && (
            <motion.div
              className="user-details"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              <img src={userData.avatar_url} alt="User Avatar" className="avatar" />
              <h2>{userData.name || 'No Name Available'}</h2>
              <p>Username: {userData.login}</p>
              <p>Location: {userData.location || 'No Location Provided'}</p>
              <p>Bio: {userData.bio || 'No Bio Available'}</p>
              <p>Public Repositories: {userData.public_repos}</p>
              <p>Followers: {userData.followers}</p>
            </motion.div>
          )}
        </AnimatePresence>
        {/* Followers Section */}
        {followers.length > 0 && (
        <div className="follower-container">
          <h2>Followers</h2>
          <div className="follower-grid">
            {followers.map((follower) => (
              <div key={follower.id} className="follower-card">
                <img src={follower.avatar_url} alt="Follower Avatar" className="follower-avatar" />
                <div className="follower-details">
                  <span className="follower-name">{follower.login}</span>
                  <span className="follower-username">@{follower.login}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
        )}
      </div>
      
      {repositories.length > 0 && (
        <motion.div
          className="repositories-container"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <h2>Repositories</h2>
          <div className="repository-grid">
            {repositories.map((repo) => (
              <div key={repo.id} className="repository-card">
                <h3>{repo.name}</h3>
                <p>{repo.description || 'No Description Available'}</p>
                <p>⭐ {repo.stargazers_count} stars</p>
              </div>
            ))}
          </div>

          {/* Pagination Controls */}
          <div className="pagination">
            <button onClick={handlePreviousPage} disabled={currentPage === 1}>
              Previous
            </button>
            <span>
              Page {currentPage} of {totalPages}
            </span>
            <button onClick={handleNextPage} disabled={currentPage === totalPages}>
              Next
            </button>
          </div>
        </motion.div>
      )}
    </motion.div>
  );
};

export default UserProfilePage;
